<template>
<!--  v-for="(item,index) in list" :key="index"  -->
  <div>
    <div v-for="(item,index) in list" :key="index">
       <div style="border-bottom:1px solid rgb(234, 227, 227); margin:0 6px;">
           <mu-row>
               <mu-col span="6" offset="">
                    <span style="color:rgb(58, 56, 56)">主题</span><span class="serveMassegesubject">&nbsp;&nbsp;&nbsp;&nbsp;{{item.subject}}</span>
               </mu-col>
           </mu-row>
           <mu-row style="margin:5px;">
               <mu-col span="8" offset="">
                   <span>{{item.content}}</span>
               </mu-col>
           </mu-row>
           <mu-row>
               <mu-col span="4" offset="" style="color:#ccc">
                   <span>留言人：</span><span>{{item.username}}</span>
               </mu-col>
               <mu-col span="6" offset="0">
                   <span style="color:#ccc">时间：</span><span style="color:rgb(123, 121, 121)">{{item.createTime}}</span>
               </mu-col>
           </mu-row>
       </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      msg: "hello 数据详情",
      // Url: "http://192.168.50.151:8800/system/feedback/list",
      Url:"http://47.112.16.168:8800/system/feedback/list",
      token: sessionStorage.getItem("token"),
      list: [],
      selects: [],
      loading:false,
    };
  },
  methods:{
       fullscreen () {
       this.loading = this.$loading();
    }
  },
  mounted() {
    this.fullscreen();
    var that = this;
    this.$http({
      url: that.Url,
      method: "get",
      headers: {
        token: that.token
      }
    })
      .then(res => {
        console.log(res);
        that.list = res.data.data;
        that.loading.close();
      })
      .catch(err => {
        console.log(err);
      });
  }
};
</script>
<style scoped>
.serveMassegesubject{
    font-size: 14px;
    font-weight: 600;
}
</style>



